/*layout 外框  header*/
.header {
  padding: 30px 88px;
  position: fixed;
  z-index: 50;
  width: 100%;
  top: 0;
  background-color: #aa0601; }
  .header .logo a {
    background-image: url(../img/logo-new.png);
    display: block;
    width: 80px;
    height: 50px;
    text-indent: 102%;
    white-space: nowrap;
    overflow: hidden;
    background-size: cover; }
  .header .header__menu {
    max-width: 512px;
    width: 100%; }
    .header .header__menu li {
      max-width: 80px;
      width: 100%;
      text-align: center; }
      .header .header__menu li a {
        display: block;
        color: #fff; }
    .header .header__menu.header__menu--active {
      max-height: 250px;
      overflow: hidden;
      transition: 0.4s; }

.hamburger {
  position: absolute;
  left: 16px;
  top: 28px;
  display: none;
  color: #fff;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center; }
  .hamburger .fa-bars {
    opacity: 1;
    transition: 0.4s; }
  .hamburger .fa-times {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: 0.4s; }
  .hamburger.hamburger--active .fa-bars {
    opacity: 0;
    transition: 0.4s; }
  .hamburger.hamburger--active .fa-times {
    opacity: 1;
    transition: 0.4s; }

@media (max-width: 991px) {
  .header {
    padding: 48px 30px; }
    .header ul {
      max-width: 440px; } }

@media (max-width: 767px) {
  .header {
    flex-direction: column;
    align-items: center;
    padding: 10px 16px; }
    .header .logo {
      margin-bottom: 10px; }
    .header .header__menu {
      display: block;
      text-align: center;
      max-height: 0;
      overflow: hidden;
      transition: 0.4s; }
      .header .header__menu li {
        border: 1px solid #fff;
        max-width: 100%; }
        .header .header__menu li a {
          padding: 16px; }
  .hamburger {
    display: block; } }

